import { BaseProduct } from '../types/scraper';
import { Platform } from 'react-native';
import AsyncStorage from '@react-native-async-storage/async-storage';

export class ProductService {
  private static isWeb = Platform.OS === 'web';

  static async getAllProducts(): Promise<BaseProduct[]> {
    try {
      if (this.isWeb) {
        // Try loading from public/data first
        try {
          const response = await fetch('/data/foils.json');
          if (response.ok) {
            const products = await response.json();
            console.log(`Loaded ${products.length} products from public/data/foils.json`);
            return products;
          }
        } catch (error) {
          console.warn('Failed to load from public/data, trying .cache directory...');
        }

        // Try loading from .cache as fallback
        try {
          const allProducts: BaseProduct[] = [];
          const brandFiles = [
            'axis-foils.json',
            'code-foils.json', 
            'kt-foils.json',
            'uni-foil.json'
          ];
          
          for (const brandFile of brandFiles) {
            try {
              const response = await fetch(`/.cache/${brandFile}`);
              if (!response.ok) continue;
              
              const products = await response.json();
              const brand = brandFile.replace('.json', '').split('-').map(
                word => word.charAt(0).toUpperCase() + word.slice(1)
              ).join(' ');
              
              if (products) {
                console.log(`Loaded ${products.length} products for ${brand} from .cache`);
                allProducts.push(...products);
              }
            } catch (error) {
              console.warn(`Error loading cache file: ${brandFile}`);
            }
          }

          if (allProducts.length > 0) {
            return allProducts;
          }
        } catch (error) {
          console.warn('Failed to load from .cache directory');
        }
      } else {
        // For mobile, use AsyncStorage
        const cached = await AsyncStorage.getItem('foils');
        if (cached) {
          const products = JSON.parse(cached);
          console.log(`Loaded ${products.length} products from AsyncStorage`);
          return products;
        }
      }
    } catch (error) {
      console.error('Error loading foils data:', error);
    }
    
    console.warn('No foils data found');
    return [];
  }
} 